import { Component, Inject, ViewEncapsulation } from '@angular/core';
import { LpxNavbarItem, UserProfileService } from '@volo/ngx-lepton-x.core';
import { OBSERVE, OBSERVE_PROVIDER, ObserveFn } from 'ng-observe';

@Component({
  selector: 'lpx-mn-user-profile',
  templateUrl: './mn-user-profile.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [OBSERVE_PROVIDER],
})
export class MnUserProfileComponent {
  user = this.observe(this.service.user$);

  get userProfile() {
    return this.user.value;
  }

  menuHidden = true;

  constructor(
    public service: UserProfileService,
    @Inject(OBSERVE) private observe: ObserveFn
  ) {}

  toggleMenu() {
    this.menuHidden = !this.menuHidden;
  }

  onActionClick(item: LpxNavbarItem) {
    item.action?.();
  }
}
