import { Component, Inject, ViewEncapsulation } from '@angular/core';
import { LanguageService, LpxLanguage } from '@volo/ngx-lepton-x.core';
import { OBSERVE, OBSERVE_PROVIDER, ObserveFn } from 'ng-observe';

@Component({
  selector: 'lpx-mn-language-selection',
  templateUrl: './mn-language-selection.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [OBSERVE_PROVIDER],
})
export class MnLanguageSelectionComponent {
  availableLanguages = this.observe(this.service.languages$);
  selectedLanguage = this.observe(this.service.selectedLanguage$);

  get langs() {
    return this.availableLanguages.value;
  }

  menuHidden = true;

  constructor(
    private service: LanguageService,
    @Inject(OBSERVE) private observe: ObserveFn
  ) {}

  toggleMenu() {
    this.menuHidden = !this.menuHidden;
  }

  onLanguageSelection(lang: LpxLanguage) {
    this.service.setSelectedLanguage(lang);
  }
}
